$darkFontColor: #153E45;
$lightColor: #FFE6DF;
$redColor3: #da5829;

.about-block {
    padding: 20px;
    .text-about {
        margin: 50px auto;
        max-width: 800px;
        text-align: center;
        line-height: 1.5;
        font-size: 20px; }

    .text-about::selection {
        color: $lightColor;
        background-color: $darkFontColor; }

    .contacts-block {
        display: flex;
        margin-bottom: 50px;
        width: 80vw;
        color: $darkFontColor;
        @media (max-width: 870px) {
            flex-direction: column; }

        .contact-block {
            font-size: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            .name-span {
                margin-top: 20px;
                margin-bottom: 20px; }
            > span {
                display: block;
                margin-bottom: 10px; }
            > a {
                display: block;
                text-decoration: none;
                color: $darkFontColor;
                margin-bottom: 7px;
                &:hover {
                    color: $redColor3; } } }
        .map {
            margin: 20px;
            width: 50vw;
            @media (max-width: 870px) {
                width: 70vw;
                margin-top: 40px; }
            > iframe {
                width: 50vw;
                height: 350px;
                @media (max-width: 870px) {
                    width: 70vw; } } } } }

