$defaultFontSize: 18px;
$seaColor: #007083;
$redColor: #D97354;
$darkFontColor: #153E45;
$lightColor: #FFE6DF;
$redColor3: #da5829;

.footer-block {
    background-color: $seaColor;
    min-height: 100px;
    display: flex;
    align-items: center;

    .wrapper-footer-block {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: 425px) {
            flex-direction: column;
            margin-top: 20px; } }

    .footer-logo {
        font-size: 30px;

        a {
            text-decoration: none;
            color: $redColor; } }

    .footer-contact-block {
        color: $lightColor;
        margin-top: 40px;
        margin-bottom: 50px;
        @media (max-width: 700px) {
            width: 200px; }
        @media (max-width: 700px) {
            width: 150px; }
        @media (max-width: 425px) {
            margin-top: 15px;
            width: 300px; }
        > span {
            display: block;
            margin-bottom: 8px; }
        > a {
            color: $redColor3;
            text-decoration: none;
            font-size: 20px; } } }
