$defaultFontSize: 18px;
$seaColor: #007083;
$redColor: #D97354;
$darkFontColor: #153E45;
$lightColor: #FFE6DF;
$redColor3: #da5829;
$yellowFon: #e8ebcc;
$blueForFon: #d8e5e6d3;
$twooForFon: #eee9e69c;

%button {
    border: 0;
    font-size: 20px;
    padding: 10px 20px;
    text-transform: uppercase;
    font-weight: bold; }

.app {
    background-color: $seaColor; }

.button {
    @extend %button; }

.container {
  max-width: 1440px;
  width: 90%;
  margin-right: auto;
  margin-left: auto; }

button {
    padding: 20px 50px;
    margin: 10px;
    text-transform: uppercase;
    border: none;
    cursor: pointer;
    &:hover {
        box-shadow: inset 2px 2px 5px rgba(154, 147, 140, 0.5), 1px 1px 5px rgba(255, 255, 255, 1); } }
