$defaultFontSize: 18px;
$seaColor: #007083;
$redColor: #D97354;
$darkFontColor: #153E45;
$lightColor: #FFE6DF;
$redColor3: #da5829;
$yellowFon: #e8ebcc;

nav {
    list-style: none; }

.li {
    display: inline;
    padding: 10px 25px;
    cursor: pointer;
    text-transform: uppercase;
    color: $lightColor;
    font-weight: 600;
    font-size: 20px;
    text-decoration: none;
    text-shadow: $darkFontColor 0 0 15px; }
.li.active {
    border-bottom: solid; }

.maxNavBlock {}

.minNavBlock {}

@media (max-width: 1060px) {
    .inherit {
        display: none; }
    .ppp {
        display: block;
        position: absolute;
        right: 30px;
        z-index: 3;
        background-color: rgba(167, 165, 165, 0.664);
        .li {
            display: block;
            &:hover {
                background-color: $redColor; } } } }
