$defaultFontSize: 18px;
$seaColor: #007083;
$redColor: #D97354;
$darkFontColor: #153E45;
$lightColor: #FFE6DF;

.header-block {
    background-color: $seaColor;
    min-height: 100px; }

.header-block .logo {
    height: 80px;
    margin: 10px;

    &:hover {
        cursor: pointer; }

    &.active {
        background-color: black; } }

.wrapper-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: {
        right: 20px;
        top: 10px;
        bottom: 10px; } }

.menu {
    display: none;
    cursor: pointer; }

.delete {
    height: 30px; }

@media (min-width: 1060px) {
    .delete {
        display: none; } }
@media (max-width: 1060px) {
    .menu {
        display: block; } }
