$yellowFon: #e8ebcc;
$darkFontColor: #153E45;
$redColor3: #da5829;
$lightColor: #FFE6DF;


.wrapper-articles-page {
    position: relative;
    margin: 110px auto;

    .api {
        width: 150px;
        padding: 20px;
        position: absolute;
        right: 1px;
        top: 1px;
        margin-top: -40px;
        margin-right: -40px;
        box-shadow: 0 0 10px rgba(0,0,0,0.5);
        border-radius: 10px;
        background-color: #da5829;
        color: #FFE6DF;

        .item-api {
            span {
                display: block; }
            .author {
                font-style: italic;
                padding-top: 5px; } } }

    .wrapper-articles {
        display: flex;
        margin: 80px 0px;

        .articles-block {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            align-items: stretch;

            .one {
                height: 600px;
                width: 40%;
                @media (max-width: 375px) {
                    width: 70%;
                    margin-top: 80px; }

                .article-text-block {
                    height: 70%;
                    @media (max-width: 1400px) {
                        height: 55%; }
                    @media (max-width: 800px) {
                        height: 60%; } } }
            .two {
                height: 280px;
                .article-text-block {
                    height: 65%;
                    @media (max-width: 1500px) {
                        height: 40%; }
                    @media (max-width: 1120px) {
                        height: 37%; }
                    @media (max-width: 1100px) {
                        height: 27%; }
                    @media (max-width: 900px) {
                        height: 65%; } } }

            .three {
                height: 280px;
                .article-text-block {
                    height: 65%;
                    @media (max-width: 1220px) {
                        height: 40%; }
                    @media (max-width: 900px) {
                        height: 65%; } } }

            .four {
                width: 100%;
                height: 350px;
                .article-text-block {
                    height: 70%;
                    @media (max-width: 850px) {
                        height: 50%; }
                    @media (max-width: 700px) {
                        height: 70%; } } } } } }

.sp-block {
    width: 50%; }

.article {
    overflow: hidden;
    margin: 15px;
    color: #153E45;
    padding: 7px;
    position: relative;

    > a {
        cursor: pointer;
        text-decoration: none;
        color: #153E45;
        &:hover {
            color: $redColor3; }
        > h2 {
            padding-bottom: 20px;
            font-size: 35px;
            line-height: 1.4; } } }

.article-text-block {
    font-size: 25px;
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 1.5;
    margin-bottom: 40px;
    text-indent: 60px;
    @media (max-width: 800px) {
        line-height: 1.3; }
    @media (max-width: 425px) {
        font-size: 16px; } }

.read-next {
    position: absolute;
    bottom: 5px; }

@media (max-width: 1500px) {
    .sp-block {
        width: 40%; } }

@media (max-width: 900px) {
    .sp-block {
        width: 100%; } }

@media (max-width: 780px) {
    .article {
        > a {
            > h2 {
                font-size: 27px; } } } }

@media (max-width: 550px) {
    .article {
        > a {
            > h2 {
                font-size: 22px; } }
        .article-text-block {
            font-size: 16px; } } }

@media (max-width: 425px) {
    .article {
        margin: 15px 6px;
        max-width: 270px;
        > a {
            > h2 {
                font-size: 18px; } }
        .article-text-block {
            font-size: 16px; } } }


.article-block-item {
    min-height: 60vh;

    .wrapper-article-block {
        margin: 90px auto;

        .article-item {
            margin: 10% 15%;
            color: #153E45;
            @media (max-width: 425px) {
                margin: 10% 5%; }

            > h2 {
                margin-bottom: 40px;
                font-size: 35px; } } } }


