$yellowFon: #e8ebcc;
$darkFontColor: #153E45;
$redColor3: #da5829;

.switch-btn {
    display: inline-block;
    width: 72px;
    height: 28px;
    border-radius: 19px;
    background: #bfbfbf;
    z-index: 0;
    margin: 0;
    padding: 0;
    border: none;
    cursor: pointer;
    position: relative;
    transition-duration: 300ms;
    margin: 10px; }

.switch-btn::after {
    content: "";
    height: 22px;
    width: 32px;
    border-radius: 17px;
    background: #fff;
    top: 3px;
    left: 3px;
    transition-duration: 300ms;
    position: absolute;
    z-index: 1; }

.switch-on {
    background: #118c4e; }

.switch-off::after {
    left: 37px; }

.services-block {}

.wrapper-services-block {
    display: flex;
    flex-flow: wrap;
    flex-grow: 1;
    justify-content: space-around;
    margin: 110px auto;

    .service-block {
        margin: 20px 10px;
        max-width: 400px;
        height: 400px;
        color: #2d433c;
        padding: 20px;
        box-shadow: 0 0 10px $darkFontColor;
        border-radius: 5px;
        position: relative;

        > a {
            cursor: pointer;
            text-decoration: none;
            color: #153E45;
            &:hover {
                color: $redColor3; } }

        > p {
            overflow-y: hidden;
            max-height: 250px;
            padding: 4px;
            margin-top: 15px;
            font-size: 15px;
            line-height: 1.5; }

        > span {
            position: absolute;
            bottom: 15px; } } }

.service-block-item {
    min-height: 60vh;
    @media (max-width: 425px) {
        min-height: 70vh; }

    .wrapper-service-block {
        .service-item {
            margin: 10% 15%;
            color: #153E45;
            @media (max-width: 425px) {
                margin: 10% 5%; }

            > h2 {
                margin-bottom: 40px;
                font-size: 35px;
                @media (max-width: 425px) {
                    font-size: 25px; } }

            > p {
                line-height: 1.5;
                margin-bottom: 40px;
                font-size: 25px;
                text-indent: 60px;
                @media (max-width: 425px) {
                    font-size: 20px; } } } } }

.block-for-like {
    display: flex;
    align-items: center;
    margin: 10px 0;
    > span {
        display: block; }
    > img {
        cursor: pointer;
        margin-left: 4px; } }
