$darkFontColor: #153E45;

.tears-block {
    display: flex;
    align-items: center; }

.wrapper-tears-block {
    max-width: 1100px;
    font-size: 20px;
    margin: 110px auto;
    color: #153E45;
    line-height: 1.5;
    @media (max-width: 375px) {
            font-size: 18px; }

    > h2 {
        margin-bottom: 40px;
        @media (max-width: 570px) {
            text-align: center; } }
    > p {
        margin-bottom: 15px;
        text-indent: 60px; } }


