$defaultFontSize: 18px;
$seaColor: #007083;
$redColor: #D97354;
$darkFontColor: #153E45;
$lightColor: #FFE6DF;
$redColor3: #da5829;
$yellowFon: #e8ebcc;

.main-block {
    min-height: 88vh;
    background-color: $yellowFon;
    background: url(../../../assets/img/fon.jpg) no-repeat center top fixed;
    background-size: cover;
    display: flex;
    align-items: center;
    padding: 15px;

    .content-main-block {
        max-width: 1000px;
        margin-bottom: 50px;

        > h1 {
            font-size: 60px;
            line-height: 1.5;
            color: $lightColor;

            @media (max-width: 800px) {
                font-size: 50px; }
            @media (max-width: 570px) {
                font-size: 40px; }
            @media (max-width: 375px) {
                font-size: 30px; } } }

    .Btn-block {
        .Btn-for-call {
            background-color: $seaColor;
            color: $lightColor; }

        .div-for-Btn-for-write {
            display: inline;

            .Btn-for-write {
                background-color: $redColor;
                color: $lightColor; } }

        @media (max-width: 800px) {
            margin-bottom: 40px;
            display: flex;
            flex-direction: column;
            align-items: center; } } }

.modal-wrapper {
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    background-color: rgba(167, 165, 165, 0.664);
    display: grid;
    justify-content: center;
    align-items: center; }

.button-modal-close {
    position: absolute;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    margin: 20px;
    border: none;
    cursor: pointer;
    box-shadow: 0 0 10px #2d433c; }

.modal-content > button {
    width: 100%;
    margin: 1px;
    background-color: $seaColor;
    color: $lightColor; }

.modal-content > span {
    display: block;
    margin: 20px auto;
    > input {
        min-height: 40px;
        padding: 8px;
        border: none;
        @media (max-width: 320px) {
            max-width: 210px; } } }

.for-textarea {
    > textarea {
        padding: 13px;
        border: none;
        @media (max-width: 320px) {
            max-width: 200px; } } }

.inheritM {
    display: none; }

