* {
  margin: 0;
  padding: 0;
  font-family: Montserrat;
}
@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat'), url(./assets/fonts/Montserrat/Montserrat-Regular.ttf) format('truetype');
}
.section-for-main-page:nth-child(2n) {
  background-color: #eee9e69c;
  padding: 40px 0;
  position: relative;
}
.section-for-main-page:nth-child(2n + 1) {
  background-color: #d8e5e6d3;
  padding: 40px 0;
  position: relative;
}
.section-for-main-page > h1 {
  text-align: center;
  margin: 20px;
}
.after::after {
  content: "";
  display: block;
  border-bottom: 2px solid #153E45;
  border-radius: 25px;
  width: 40%;
  height: 3px;
  position: absolute;
  right: 30%;
  bottom: 0;
}
.block-upward {
  position: absolute;
  position: fixed;
  left: 30px;
  bottom: 40px;
  display: block;
}
.block-upward-inherit {
  display: none;
}
.img-upward {
  color: #153E45;
}
